body {
  background-color: rgb(20,20,20);
  text-align: center;
  font-family: 'Piazzolla', serif;
  color: white;
  font-size: 20px;
}

h4 {
  font-size: 16px;
  margin: 0;
}

label {
  font-family: 'Piazzolla', serif;
  font-weight: bolder;
  font-size: 25px;
}

.picpok {
  width:  150px;
  height: 150px;
  object-fit: cover;
}

.picpokShiny {
  width:  288px;
  height: 288px;
  object-fit: cover;
}

.pictype {
  width: 89px;
  height: 39px;
}

.button {
  margin: 0px;
  font-size: 20px;
}

h1 {
  margin:0;
}

h2 {
  font-weight: 400;
  margin: 0;
  padding-bottom: 14px;
  height: 20px;
}

h3 {
  margin: 0;
  font-size: 15px;
  font-weight: 0;
}

.stats {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.shinies {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

#FP1 {
  padding-bottom:4px;
}

#FP2 {
  padding-bottom:4px;
}


#bottom {
  font-family: 'Arial', serif;
  font-size: 12px;
  position: absolute; 
  bottom: 0; 
  left: 0; 
  width: 100%; 
  text-align: center
}

.monstats {
  padding-bottom: 2px;
}

.montotal {
  padding-bottom: 10px;
}

.monab {
  padding-bottom: 8px;
}

.monweak {
  vertical-align: auto;
  padding-bottom: 4px;
}

.monweak img {
  width: 52px;
  height: 22px;
}

.green-text {
  color: green;
}

.red-text {
  color: red;
}

a:link {
  color:rgb(30,130,230);
  background-color: transparent;
  text-decoration: underline;
}

a:visited {
  color:darkorchid;
  background-color: transparent;
}

a:active {
  color:darkorchid;
  background-color: transparent;
  text-decoration: dashed;
}

input {
  font-size: inherit;
  font-family: inherit;
}

/* MOBILE STYLE */
@media only screen and (max-width: 425px) {
  .calculator {
      padding: 1rem;
  }

  .calculator>*+* {
      margin-top: 0.5rem;
  }

  .calculator input {
      box-sizing: border-box;
      padding   : 0.5rem;
      width     : 100%;
  }

  .inputs>* {
      display: block;
  }

  .inputs>*+* {
      margin-top: 0.5rem;
  }

  .buttons {
      display  : flex;
      flex-flow: row nowrap;
      gap      : 0.5rem;
  }

  .buttons>* {
      flex: 1 1 33%;
  }

  #stats {
      font-size: 0.9rem;
  }

  #shinies {
      grid-template-columns: 1fr;
  }
}

.fusion-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.pokemon-couple {
  margin: 5px;
  display: flex;
  border: 1px solid white;
}

.fusion {
  margin: 10px;
  padding: 5px;
}

.fusion ~ .fusion {
  border-left: 1px solid grey;
}

.fusion .stats-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 90px;
}